import React, {useEffect} from 'react'
import * as S from "./styles"
import LOGO from "../../img/logo.png"
import { IoMenu } from "react-icons/io5"
import { IoClose } from "react-icons/io5"
import { Link } from "react-router-dom"

export const Nav = ({ isOpen, setIsOpen }) => {

        // nav transparente quando no topo
        var show = null;

        useEffect(() => {
            const handleScroll = () => {
                var links = document.querySelectorAll(".menu-link");
    
                show = window.scrollY > 0
    
                if (show) {
                    document.querySelector("#menu").style.background = "#0D6786";
    
                } else {
                    document.querySelector("#menu").style.background = "transparent";
    
                }
            }
            document.addEventListener('scroll', handleScroll)
            return () => {
                document.removeEventListener('scroll', handleScroll)
            }
        }, [])
        // nav transparente quando no topo
        
    return (
        <>
            <S.Container id="menu">
                <S.Wrapper className="container">

                    <S.Logo>
                        <img src={LOGO} alt="logo" />
                    </S.Logo>

                    <S.Menu onClick={() => setIsOpen(!isOpen)}>
                        {isOpen ?
                            <IoClose className="icon-menu" /> :
                            <IoMenu className="icon-menu" />
                        }
                    </S.Menu>

                    <S.MenuDeskTop>
                        <nav>
                            <ul>
                                <li>
                                    <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Home</Link>
                                </li>
                                <li>
                                    <Link to="/atividades" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Atividades</Link>
                                </li>
                                <li>
                                    <Link to="/fotos" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Fotos</Link>
                                </li>
                                <li>
                                    <Link to="/parceiros" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Parceiros</Link>
                                </li>
                                <li>
                                    <Link to="/redes" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Redes Sociais</Link>
                                </li>
                                <li>
                                    <Link to="/contatos" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Contatos</Link>
                                </li>
                            </ul>
                        </nav>
                    </S.MenuDeskTop>

                </S.Wrapper>
            </S.Container>

            <S.MenuMobile isOpen={isOpen}>
                <nav>
                    <ul>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Home</Link>
                        </li>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Atividades</Link>
                        </li>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Fotos</Link>
                        </li>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Parceiros</Link>
                        </li>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Redes Sociais</Link>
                        </li>
                        <li>
                            <Link to="/" style={LinkStyle} className="link" onClick={() => setIsOpen(!isOpen)}>Contatos</Link>
                        </li>
                    </ul>
                </nav>
            </S.MenuMobile>
        </>
    )
}


const LinkStyle = {
    textDecoration: "none"
}